import React, { Component } from 'react';
import { Jumbotron, Col, Row, Fade, Button } from 'reactstrap';
import { Draggable, Droppable } from 'react-drag-and-drop';
import Locker from '../images/slaughter-mask02.png';
import Stunner from '../images/baton.png';
import StunToolModal from './StunToolModal';
import Club from '../images/bolter-gun.png';

export default class StunPig extends Component {
  constructor(props) {
    super(props);
    this.state = { fadeIn: true };
    this.toggle = this.toggle.bind(this);
  }
  //////vvvvvSTATEvvvvvSTATEvvvvvSTATE///

  state = {
    jumbotron1: `We're going to process a single medium sized pig from snout to tail.`,
    doorCollapsed: false,
    defaultLead: `
        
        `,
    selectedTool: 'head slam',
    locker: `url(${Locker})`,
    stunner: `url(${Stunner})`,
    club: `url(${Club})`,
    textClub: 'Club',
    textMask: `Slaughter-mask`,
    textGun: 'Gun'
  };
  //////^^^^^STATE^^^^^STATE^^^^^STATE

  fadeInDoor = () => {
    // Set the opacity of the element to 0
    this.setState({ doorCollapsed: true });
    // alert('hear door');
  };

  updateJumbotron = pigStatus => {
    console.log(pigStatus.age);
    console.log(pigStatus.weight);
    console.log(pigStatus.health);

    this.setState({
      jumbotron1: 'Age:    ' + pigStatus.age,
      jumbotron2: 'Weight:  ' + pigStatus.weight,
      jumbotron3: 'Health:  ' + pigStatus.health
    });
  };

  toggle() {
    this.setState({
      fadeIn: !this.state.fadeIn
    });
    setTimeout(function() {
      window.open('./stunpig');
    }, 500);
  }
  dropPig = () => {};

  //vvvvv/RENDER\vvvvv/RENDER\vvvvv/RENDER\

  render() {
    var toolNames = [
      'Head Slam',
      'Electrical Stunner',
      'Club',
      'Slaughter Mask'
    ];
    var toolInfo = [
      `You have chosen to slam this animal's head against the ground until it is unconscious.`,
      `You have chosen to pass an electric current through the brain of this animal.`,
      `You have chosen to stun this animal with a direct blow to skull using a club. The blow must be dealt with precision and force, so that the skull is immediately smashed, causing instantaneous unconsciousness. The blow should be aimed slightly above the eyes.`,
      `You have chosen to stun the animal using a Slaughter mask. A bolt held by the mask will be driven into the animal's brain by a hammer blow. The device is fitted with a spring which returns the bolt to its original position.`
    ];
    return (
      <Fade in={this.state.fadeIn}>
        <div>
          <div id="isolated-animal">
            <div id="pig-to-kill">
              <Droppable>
                <div onClick={this.killPig} />
              </Droppable>
            </div>
          </div>
          <Jumbotron
            id="stunbowtron"
            style={{ margin: '50px 0px 50px 0', margin: 0 }}
          >
            <h5
              className="display-4"
              align="right"
              style={{ paddingBottom: '10px', margin: '0 20% 0 0' }}
            >
              Choose how to stun the animal
            </h5>

            <Row>
              <div className="tools col-12">
                <div className="kill-with">
                  <StunToolModal
                    toolName={toolNames[0]}
                    whichToolImg={'images/slam2.png'}
                    toolInfo={toolInfo[0]}
                  />
                  <br /> <h5>Head Slam</h5>
                </div>{' '}
                <div className="kill-with">
                  <StunToolModal
                    toolName={toolNames[1]}
                    whichToolImg={'images/bolter-gun.png'}
                    toolInfo={toolInfo[1]}
                  />
                  <br /> <h5>Stun-Gun</h5>
                </div>{' '}
                <div className="kill-with">
                  <StunToolModal
                    toolName={toolNames[2]}
                    whichToolImg={'images/baton.png'}
                    toolInfo={toolInfo[2]}
                  />
                  <br /> <h5>Club</h5>
                </div>{' '}
                <div className="kill-with">
                  <StunToolModal
                    toolNames={toolNames[3]}
                    whichToolImg={'images/slaughter-mask02.png'}
                    toolInfo={toolInfo[3]}
                  />
                  <br /> <h5>Slaughter-Mask</h5>
                </div>
                {/* </div> */}
                <div
                  style={{
                    // border: '2px solid red',
                    display: 'flex',
                    justifyContent: 'space-evenly',
                    marginTop: '-40px'
                  }}
                />
              </div>
            </Row>
          </Jumbotron>

          {/* </Container> */}
        </div>
      </Fade>
    );
  }
  //^^^^^/RENDER\^^^^^/RENDER\^^^^^/RENDER\
}
